<template>
    <panel :config="config" class="collection" v-if="rankables">
        <template slot="header">{{ title }}</template>
        <template slot="body">
            <a v-for="game in user_games" :key="game.id" :href="isRankable(game.id) ? urlFor('rankings-game', {'id': game.id}) : ''" :class="isRankable(game.id) ? 'clickable' : 'unclickable'" :target="isIn3rdParty ? '_blank' : '_self'"><img class="game icon" :src="game.icon" :title="game.name" width="80px" height="80px" v-tooltip.bottom="game.name" /></a>
        </template>
    </panel>
</template>

<script>
import HTTP from '@/httpapi';
import Games from '@api/games';
import GenericMixin from '@mixins/generic';

export default {
    name: 'user_collection',

    mixins: [GenericMixin],

    computed: {
        user_games () {
            return this.config.user_games;
        }
    },

    methods: {
        getRankableGames () {
            let t = this;
            Games.rankables().then(function (rankables) {
                t.rankables = rankables.data.rankingGames;
            });
        },

        isRankable (gameId) {
            return (this.rankables && this.rankables.findIndex(function (gid) { return gid === gameId; }) >= 0);
        }
    },

    mounted () {
        HTTP.ifInitialized(this.getRankableGames);
    },

    data () {
        return {
            rankables: null
        };
    }
};

</script>

<style lang="scss" scoped>
@import '~sass/_variables.scss';
@import '~sass/image.scss';

.panel.collection /deep/ {
    .panel-heading {
        h1 {
            font-size: 1.5em;
            border-bottom: none;
        }
    }

    a img.icon {
        cursor: pointer;
    }
    a:hover {
        background-color: $panel-background-color;
    }

    img.icon {
        margin: 5px;
    }

    @include media-breakpoint-down(xs) {
        img.icon {
            width: 30px;
            height: 30px;
        }
        .panel-body {
            padding: 0;
        }
    }
}

.tab-pane p {
    margin: 1em;
}

.nav.nav-tabs li {
    a {
        background: none;
        background-color: white;
        border-bottom: 1px solid $border-gray;
    }
    &.active a {
        background: none;
        border: 1px solid $border-gray;
        border-bottom: 1px solid white;
    }
}
</style>



